





























































import { userAdmin } from "@/api/mock";
import { defineComponent, reactive, ref } from "@vue/composition-api";

export default defineComponent({
  components: {
    AppButton: () => import("@/views/widget/app-button.vue"),
    AppButtonGold: () => import("@/views/widget/app-button-gold.vue"),
    Localization: () => import("../widget/app-localization.vue")
  },

  setup() {
    const model = reactive({
      phone: userAdmin.phone,
      password: userAdmin.password
    });

    const loading = ref(false);
    const showPassword = ref(false);

    async function login() {
      // $store
      await this.$store.dispatch("LoginByPhone", {
        phone: model.phone,
        password: model.password
      });
      await this.$router.push(this.$route.query.redirect || "/");
    }

    function register() {
      console.log("register");
      // kk
    }

    return {
      model,
      loading,
      showPassword,
      login,
      register
    };
  }
});
